@use '../util';

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 55.4em;
  margin: 0;
  padding: 0;
  background-color: var(--l-grayish-blue);

  display: flex;
  justify-content: center;
  align-items: center;
}
